.hero {
  height: 200px;
  background-size: cover;
  background-position: center;
  position: relative;
  background:#00008b;
  border-bottom: 8px solid #8B0000;
  margin-bottom: 20px;
}

.hero::after {
  content: "";
  display: block;
  opacity: .4;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: black;
}

.hero * {
  position: relative;
  z-index: 2;
}

.hero h1, .hero h2, .hero h3, .hero h4 {
  color: white;
}

.hero h1, .hero h2 {
  font-size: 40px;
  padding-top: 30px;
  margin-top: 0;
}

.hero h3, .hero h4 {
  font-size: 20px;
  padding-top: 10px;
  margin-top: 0;
}

span:hover:after {
  font-size: 120%;
  margin-right: 0;
}
